import { useEffect, useState } from 'react';

// @mui
import {
  Box,
  styled,
  Drawer,
  IconButton,
  useTheme
} from '@mui/material';
import MenuTwoToneIcon from '@mui/icons-material/MenuTwoTone';

//third-party
import { faker } from '@faker-js/faker';
import { sample } from 'lodash';

// project
import ChatTopBar from './sections/ChatTopBar.js';
import ChatBottomBar from './sections/ChatBottomBar.js';
import ChatSidebar from './sections/ChatSidebar.js';
import ChatContent from './sections/ChatContent.js';
import { Scrollbar } from 'components/Scrollbars/index.js';

// mocks
import { CHATS } from '__mocks__/chats'

const RootWrapper = styled(Box)(
  ({ theme }) => `
       height: calc(100vh - ${theme.palette['header'].height});
       display: flex;
`
);

const Sidebar = styled(Box)(
  ({ theme }) => `
        width: 320px;
        background: ${theme.palette.grey[100]};
        border-right: ${theme.palette.grey[300]} solid 1px;
`
);

const ChatWindow = styled(Box)(
  () => `
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        flex: 1;
`
);

const TopBar = styled(Box)(
  ({ theme }) => `
        background: ${theme.palette.grey[100]};
        border-bottom: ${theme.palette.grey[300]} solid 1px;
        padding: 2;
        align-items: center;
`
);

const IconButtonToggle = styled(IconButton)(
  ({ theme }) => `
  width: ${theme.spacing(4)};
  height: ${theme.spacing(4)};
  background: ${theme.palette.grey[100]};
`
);

const DrawerWrapperMobile = styled(Drawer)(
  () => `
    width: 340px;
    flex-shrink: 0;

  & > .MuiPaper-root {
        width: 340px;
        z-index: 3;
  }
`
);
const curSender = CHATS[0];
const initialChaterId = CHATS[0].chaters[0].id;// CHATS[0].chaters[0];

function Chat() {
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = useState(false);
  const [chaterId, setChaterId] = useState(initialChaterId);
  const [chater, setChater] = useState(curSender.chaters[0])
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };


  // const filterChater = useMemo(() =>
  //   curSender.chaters?.find(chater => chater.id === chaterId)
  //   , [chaterId]);

  useEffect(() => {
    const filterChater = curSender.chaters?.find(chater => chater.id === chaterId)
    setChater(filterChater)
  }, [chaterId]);

  const handleChangeChater = (chaterId) => {
    setChaterId(chaterId);
  }
  const handleContent = (content) => {

    if (content.trim().length > 0) {
      const newConent = {
        id: faker.datatype.uuid(),
        to: null,
        from: faker.datatype.uuid(),
        content,
        createdAt: new Date(),
        type: sample(['read', 'unread']),
      }
      const newChater = Object.create(chater)
      newChater.contents.push(newConent);
      setChater(newChater)
    }
  }

  return (
    <>
      <RootWrapper className="Mui-FixedWrapper">
        <DrawerWrapperMobile
          sx={{
            display: { lg: 'none', xs: 'inline-block' }
          }}
          variant="temporary"
          anchor={theme.direction === 'rtl' ? 'right' : 'left'}
          open={mobileOpen}
          onClose={handleDrawerToggle}
        >
          <Scrollbar>
            <ChatSidebar sender={curSender} handleChangeChater={handleChangeChater} />
          </Scrollbar>
        </DrawerWrapperMobile>
        <Sidebar
          sx={{
            display: { xs: 'none', lg: 'inline-block' }
          }}
        >
          <Scrollbar>
            <ChatSidebar sender={curSender} handleChangeChater={handleChangeChater} />
          </Scrollbar>
        </Sidebar>
        <ChatWindow>
          <TopBar
            sx={{
              display: { xs: 'flex', lg: 'inline-block' }
            }}
          >
            <IconButtonToggle
              sx={{
                display: { lg: 'none', xs: 'flex' },
                mr: 2
              }}
              color="primary"
              onClick={handleDrawerToggle}
              size="small"
            >
              <MenuTwoToneIcon />
            </IconButtonToggle>
            <ChatTopBar chater={chater} />
          </TopBar>
          <Box flex={1}>
            <Scrollbar>
              <ChatContent sender={curSender} chater={chater} />
            </Scrollbar>
          </Box>

          <ChatBottomBar sender={curSender} handleNewContent={handleContent} />
        </ChatWindow>
      </RootWrapper>
    </>
  );
}

export default Chat;
