import {
  Avatar,
  Tooltip,
  IconButton,
  Box,
  Button,
  styled,
  useTheme,
  TextField
} from '@mui/material';
import AttachFileTwoToneIcon from '@mui/icons-material/AttachFileTwoTone';
import { useRef } from 'react';
import { KeyboardVoice } from '@mui/icons-material';


const Input = styled('input')({
  display: 'none'
});

function ChatBottomBar(props) {
  const { sender, handleNewContent } = props;
  const theme = useTheme();
  const contentInputRef = useRef();


  const handleContent = () => {
    handleNewContent(contentInputRef.current.value)
    contentInputRef.current.value = '';
  }
  return (
    <Box
      sx={{
        background: theme.palette.grey[100],
        display: 'flex',
        alignItems: 'center',
        p: 2
      }}
    >
      <Box flexGrow={1} display="flex" alignItems="center">
        <Avatar
          sx={{ width: 48, height: 48, display: { xs: 'none', sm: 'flex' }, mr: 1 }}
          alt={sender.name}
          src={sender.avatarUrl}
        />
        <TextField
          inputRef={contentInputRef}
          label=""
          // id="outlined-basic" 

          variant="outlined"
          placeholder="Type a message here..."
          sx={{
            fontSize: theme.typography.pxToRem(18),
            padding: theme.spacing(1),
            width: '100%'

          }} />
        {/* <MessageInputWrapper
          autoFocus
          placeholder="Write your message here..."
          fullWidth
        /> */}

      </Box>
      <Box>

        <Input accept="image/*" id="messenger-upload-file" type="file" />
        <Tooltip arrow placement="top" title="Attach a file">
          <label htmlFor="messenger-upload-file">
            <IconButton sx={{ mx: 1 }} color="primary" component="span">
              <AttachFileTwoToneIcon fontSize="small" />
            </IconButton>
          </label>
        </Tooltip>
        <Tooltip arrow placement="top" title="Choose an emoji">
          <IconButton
            sx={{ fontSize: theme.typography.pxToRem(16) }}
            color="primary"
          >
            <KeyboardVoice />
          </IconButton>
        </Tooltip>
        <Button variant="contained" onClick={handleContent}>
          Send
        </Button>
      </Box>
    </Box>
  );
}

export default ChatBottomBar;
