import { useState } from 'react';
import {
  Box,
  Typography,
  TextField,
  IconButton,
  InputAdornment,
  Avatar,
  List,
  ListItemAvatar,
  ListItemText,
  styled,
  ListItem
} from '@mui/material';
import SearchTwoToneIcon from '@mui/icons-material/SearchTwoTone';
import { last } from 'lodash'
import { fShortDate } from '../../../utils/formatTime';
import { useTheme } from '@emotion/react';
import { Settings } from '@mui/icons-material';
const RootWrapper = styled(Box)(
  ({ theme }) => `
        padding: ${theme.spacing(2.5)};
  `
);

// const ListItemWrapper = styled(ListItemButton)(
//   ({ theme }) => `
//         &.MuiButtonBase-root {
//             margin: ${theme.spacing(1)} 0;
//         }
//   `
// );

function ChatSidebar(props) {
  const theme = useTheme();
  const { sender, handleChangeChater } = props;
  const [selectedChater, setSelectedChater] = useState(0)

  const handleChaterSelect = (selected, chaterId) => {
    handleChangeChater(chaterId);
    setSelectedChater(selected)
  }
  return (
    <RootWrapper>
      <Box display="flex" alignItems="flex-start">
        <Avatar alt={sender.name} src={sender.avatarUrl} sx={{ width: 48, height: 48 }} />
        <Box
          sx={{
            ml: 1.5,
            flex: 1
          }}
        >
          <Box
            display="flex"
            alignItems="flex-start"
            justifyContent="space-between"
          >
            <Box>
              <Typography variant="h5" noWrap>
                {sender.name}
              </Typography>
              <Typography variant="subtitle1" noWrap>
                {sender.title}
              </Typography>
            </Box>
            <IconButton
              sx={{
                p: 1
              }}
              size="small"
            >
              <Settings />
            </IconButton>
          </Box>
        </Box>
      </Box>

      <TextField
        sx={{
          mt: 2,
          mb: 1
        }}
        size="small"
        fullWidth
        InputProps={{
          startAdornment: (
            <InputAdornment position="start">
              <SearchTwoToneIcon />
            </InputAdornment>
          )
        }}
        placeholder="Search Contacts..."
      />
      <Box mt={2}>
        <List disablePadding component="div">
          {sender.chaters.map((chat, index) => (
            // <ListItemWrapper key={index} selected={index === selectedChater} onClick={() => handleChaterSelect(index, chat.id)}>
            <ListItem key={index} selected={index === selectedChater} onClick={() => handleChaterSelect(index, chat.id)}

              secondaryAction={
                <Typography variant='body1' noWrap>
                  {fShortDate(last(chat.contents).createdAt)}
                </Typography>
              }
              sx={{
                margin: `${theme.spacing(1)} 0`
              }}
            >
              <ListItemAvatar>
                <Avatar src={chat.avatarUrl} sx={{ width: 48, height: 48 }} />
              </ListItemAvatar>
              <ListItemText
                sx={{
                  mr: 1
                }}
                primaryTypographyProps={{
                  color: 'textPrimary',
                  variant: 'h5',
                  noWrap: true
                }}
                secondaryTypographyProps={{
                  color: 'textSecondary',
                  noWrap: true
                }}
                primary={chat.name}
                secondary={last(chat.contents).content}
              />
            </ListItem>
          ))}
        </List>
      </Box>
    </RootWrapper>
  );
}

export default ChatSidebar;
